.btn {
  padding: 4px 40px 4px 12px;
  display: flex;
  align-items: center;
  font-size: 12px;
  line-height: 24px;
  text-align: right;
  letter-spacing: -0.15px;
  text-transform: uppercase;
  color: #fff;
  border-radius: 16px;
  background-color: #FF3E79;
